export default {
	props:{
		tabBars:Array,
		tabIndex:Number,
		scrollStyle:{
			type:String,
			default:""
		},
		scrollItemStyle:{
			type:String,
			default:""
		}
	},
	methods:{
		tabtap(index){
			//点击tab时把自己的index传给父组件index.vue
			this.$emit('tabtap',index);
		},
	}
}
